import { playMode } from '../utils/constant';

export default {
    // 
    userInfo: getInfo(),
    // 
    sequenceList: [],
    // 
    mode: playMode.sequence,
    // 
    playList: [],
    // 
    currentIndex: -1,
    // 
    playing: false,
    // 
    currentTime: 0
}

function getInfo () {
    if (document.cookie.indexOf('MUSIC_U') !== -1) {
        return JSON.parse(window.localStorage.getItem('userInfo'))
    } else {
        window.localStorage.setItem('userInfo',null)
        return null
    }
}
