import layout from '../views/layout/layout'

/**
 * 
 */
export const loginRouter = [
    {
        path: '/login',
        name: 'login',
        meta: {
            title: '',
            isLogin: true
        },
        component: () => import(/* webpackChunkName: "login" */ '../views/login/login')
    }
]

/**
 * 
 */
export const appRouter = [
    {
        path: '/',
        redirect: {
            name: 'home'
        },
        meta: {
            title: '',
            keepAlive: true
        },
        component: layout,
        children: [
            {
                path: 'home',
                meta: {
                    title: '',
                    keepAlive: true
                },
                name: 'home',
                component: () => import(/* webpackChunkName: "home" */ '../views/home/home')
            },
            {
                path: 'playlist',
                meta: {
                    title: '',
                    keepAlive: true
                },
                name: 'playlist',
                component: () => import(/* webpackChunkName: "playlist" */ '../views/playlist/playlist')
            },
            {
                path: 'rank',
                meta: {
                    title: '',
                    keepAlive: true
                },
                name: 'rank',
                component: () => import(/* webpackChunkName: "rank" */ '../views/rank/rank')
            },
            {
                path: 'singer',
                meta: {
                    title: '',
                    keepAlive: true
                },
                name: 'singer',
                component: () => import(/* webpackChunkName: "singer" */ '../views/singer/singer')
            },
            {
                path: 'song',
                meta: {
                    title: '',
                    keepAlive: true
                },
                name: 'song',
                component: () => import(/* webpackChunkName: "song" */ '../views/song/song')
            },
            {
                path: 'song-detail',
                meta: {
                    title: '',
                    keepAlive: true
                },
                name: 'song-detail',
                component: () => import(/* webpackChunkName: "SongDetail" */ '../views/detail/SongDetail')
            },
            {
                path: 'playlist-detail',
                meta: {
                    title: '',
                    keepAlive: true
                },
                name: 'playlist-detail',
                component: () => import(/* webpackChunkName: "PlaylistDetail" */ '../views/detail/PlaylistDetail')
            },
            {
                path: 'singer-detail',
                meta: {
                    title: '',
                    keepAlive: true
                },
                name: 'singer-detail',
                component: () => import(/* webpackChunkName: "SingerDetail" */ '../views/detail/SingerDetail')
            },
            {
                path: 'album-detail',
                meta: {
                    title: '',
                    keepAlive: true
                },
                name: 'album-detail',
                component: () => import(/* webpackChunkName: "AlbumDetail" */ '../views/detail/AlbumDetail')
            },
            {
                path: 'user-detail',
                meta: {
                    title: '',
                    keepAlive: true
                },
                name: 'user-detail',
                component: () => import(/* webpackChunkName: "UserDetail" */ '../views/detail/UserDetail')
            },
            {
                path: 'search',
                meta: {
                    title: '',
                    keepAlive: true
                },
                name: 'search',
                component: () => import(/* webpackChunkName: "search" */ '../views/search/search')
            },
            {
                path: 'test',
                meta: {
                    title: '',
                    keepAlive: true
                },
                name: 'test',
                component: () => import(/* webpackChunkName: "test" */ '../views/test/TestCookie')
            }
        ]
    }
]

export const routes = [...loginRouter, ...appRouter]
