import iAxios from './instance'

/**
 * @method 
 * @param phone 
 * @param password 
 */
export const login = (phone, password) =>
    iAxios.get(`/login/cellphone?phone=${phone}&password=${password}`, {
        withCredentials: true
    })

/**
 * @method 
 */
export const logout = () => iAxios.get('/logout', {})

